const Razorpay = require('razorpay');
const { userModal } = require('../../../db');

const razorpay = new Razorpay({
    key_id: process.env.RAZORPAY_KEY_ID,
    key_secret: process.env.RAZORPAY_KEY_SECRET,
});

export default async function handler(req, res) {
    if (req.method !== 'POST') {
        return res.status(405).json({ error: 'Method not allowed' });
    }

    try {
        const { username } = req.body;
        if (!username) {
            return res.status(400).json({ error: 'Username is required' });
        }

        // Find user
        const user = await userModal.findOne({ username });
        if (!user) {
            return res.status(404).json({ error: 'User not found' });
        }

        // Create Razorpay order
        const options = {
            amount: 9900, // amount in paise (â‚¹99)
            currency: 'INR',
            receipt: `receipt_${username}_${Date.now()}`,
            payment_capture: 1,
        };
        const order = await razorpay.orders.create(options);

        // Store order id in user record for later verification (optional)
        user.razorpayOrderId = order.id;
        await user.save();

        res.status(200).json({ orderId: order.id, keyId: process.env.RAZORPAY_KEY_ID });
    } catch (error) {
        console.error('Razorpay order creation error:', error);
        res.status(500).json({ error: error.message });
    }
}
